import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { signInWithEmailAndPassword, signInWithPopup, signInWithRedirect, getRedirectResult } from "firebase/auth";
import { auth, db, googleProvider } from "../firebase";
import { doc, setDoc, serverTimestamp, getDoc } from "firebase/firestore";
import { alertInfo } from "../utils/commonUtils";
import googleLogo from "../img/g-logo.png";

async function upsertUserDoc(u) {
  const ref = doc(db, "users", u.uid);
  const snap = await getDoc(ref);
  await setDoc(
    ref,
    {
      uid: u.uid,
      email: u.email || "",
      name: u.displayName || "",
      photoURL: u.photoURL || "",
      role: snap.exists() ? snap.data().role || "user" : "user",
      lastLogin: serverTimestamp(),
      creDate: snap.exists() ? snap.data().createdAt || serverTimestamp() : serverTimestamp()
    },
    { merge: true }
  );
}

export default function Login() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    (async () => {
      try {
        const cred = await getRedirectResult(auth);
        if (cred?.user) {
          await upsertUserDoc(cred.user);
          navigate("/main");
        }
      } catch (e) {
        alertInfo(`  . (${e.code})`);
      }
    })();
  }, [navigate]);

  const handleLogin = async () => {
    try {
      setLoading(true);
      await signInWithEmailAndPassword(auth, email, pw);
      navigate("/main");
    } catch (e) {
      const invalidCode = [
        "auth/invalid-login-credentials",
        "auth/invalid-credential",
        "auth/user-not-found",
        "auth/wrong-password"
      ];
      if (invalidCode.includes(e.code)) alertInfo("    .");
      else if (e.code === "auth/network-request-failed") alertInfo("   .");
      else if (e.code === "auth/invalid-email") alertInfo("  .");
      else if (e.code === "auth/internal-error") alertInfo(" .");
      else alertInfo(" .");
    } finally {
      setLoading(false);
    }
  };

  const handleGoogleLogin = async () => {
    try {
      setLoading(true);
      const cred = await signInWithPopup(auth, googleProvider);
      await upsertUserDoc(cred.user);
      navigate("/main");
    } catch (e) {
      if (e.code === "auth/popup-closed-by-user") {
        alertInfo(" ");
        return;
      }
      if (e.code === "auth/popup-blocked") {
        await signInWithRedirect(auth, googleProvider);
        return;
      }
      alertInfo(`  . (${e.code})`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="page">
      <div className="header"></div>
      <section className="intro-section">
        <div className="form-row">
          <input
            className="input"
            type="email"
            placeholder=" ."
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <div className="form-row">
          <input
            className="input"
            type="password"
            placeholder=" ."
            value={pw}
            onChange={(e) => setPw(e.target.value)}
          />
        </div>

        <div className="form-row" style={{ display: "flex", gap: ".5rem" }}>
          <button className="btn btn-primary" onClick={handleLogin} disabled={loading}>
            {loading ? "..." : ""}
          </button>
          <button className="btn btn-ghost" onClick={() => navigate("/signup")} disabled={loading}>
            
          </button>
          <button className="btn btn-google" onClick={handleGoogleLogin} disabled={loading}>
            <img src={googleLogo} alt="Google" />
            Google  
          </button>
        </div>
      </section>
    </div>
  );
}
