import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { auth, db } from "../firebase";
import { createUserWithEmailAndPassword, updateProfile, sendEmailVerification } from "firebase/auth";
import { doc, setDoc, serverTimestamp } from "firebase/firestore";
import { alertInfo } from "../utils/commonUtils";

export default function SignUp() {
  const navigate = useNavigate();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");
  const [pw2, setPw2] = useState("");
  const [loading, setLoading] = useState(false);

  const onSubmit = async () => {
    if (!name.trim()) return alertInfo(" ");
    if (!email.trim()) return alertInfo(" ");
    if (!pw.trim() || !pw2.trim()) return alertInfo(" ");
    if (pw.length < 6) return alertInfo(" 6  ");
    if (pw !== pw2) return alertInfo("  ");

    try {
      setLoading(true);
      const cred = await createUserWithEmailAndPassword(auth, email, pw);
      await updateProfile(cred.user, { displayName: name.trim() });
      await setDoc(doc(db, "users", cred.user.uid), {
        uid: cred.user.uid,
        email: cred.user.email,
        name: name.trim(),
        role: "user",
        creDate: serverTimestamp()
      });

      try {
        await sendEmailVerification(cred.user);
      } catch (_) {}
      alertInfo(" ", () => navigate("/login"));
    } catch (e) {
      let msg = " ";
      if (e.code === "auth/email-already-in-use") msg = "  ";
      if (e.code === "auth/invalid-email") msg = "  ";
      if (e.code === "auth/weak-password") msg = "  ";
      alertInfo(`${msg}\n(${e.code})`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="page">
      <div className="header"></div>
      <section className="intro-section">
        <div className="form-row">
          <input
            className="input"
            type="text"
            placeholder=""
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
        </div>
        <div className="form-row">
          <input
            className="input"
            type="email"
            placeholder=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <div className="form-row">
          <input
            className="input"
            type="password"
            placeholder=" (6 )"
            value={pw}
            onChange={(e) => setPw(e.target.value)}
          />
        </div>
        <div className="form-row">
          <input
            className="input"
            type="password"
            placeholder=" "
            value={pw2}
            onChange={(e) => setPw2(e.target.value)}
          />
        </div>
        <button className="btn btn-primary" onClick={onSubmit} disabled={loading}>
          {loading ? "..." : ""}
        </button>
      </section>
    </div>
  );
}
