import { useEffect, useState } from "react";
import { doc, getDoc, updateDoc, serverTimestamp } from "firebase/firestore";
import { useNavigate, useParams } from "react-router-dom";
import { db } from "../firebase";
import { alertConfirm, alertInfo } from "../utils/commonUtils";
import { useAuth } from "../AuthContext";

export default function BoardEdit() {
  const { user } = useAuth();
  const { id } = useParams();
  const navigate = useNavigate();

  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [post, setPost] = useState(null);

  useEffect(() => {
    const loadPost = async () => {
      const docRef = doc(db, "board", id);
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) {
        const data = docSnap.data();
        setPost({ id: docSnap.id, ...data });
        setTitle(data.title || "");
        setBody(data.body || "");
      }
    };
    if (!user) {
      alertInfo(" .", () => navigate("/login"));
    } else {
      loadPost();
    }
  }, [id, user, navigate]);

  const handleUpdate = () => {
    if (!title.trim()) return alertInfo(" ");
    if (!user) return alertInfo(" .", () => navigate("/login"));
    if (!post || post.ownerUid !== user.uid) return alertInfo("    .");

    alertConfirm("?", async () => {
      const docRef = doc(db, "board", id);
      await updateDoc(docRef, {
        title,
        body,
        updateDate: serverTimestamp(),
        file: ""
      });
      alertInfo(" !");
      navigate(`/board/${id}`);
    });
  };

  return (
    <div className="page">
      <div className="header"> </div>
      <section className="intro-section">
        <div className="meta" style={{ marginBottom: ".5rem" }}>
          : <b>{post?.authName || ""}</b>
        </div>

        <div className="form-row">
          <input
            className="input"
            type="text"
            placeholder=""
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>
        <div className="form-row">
          <textarea
            className="textarea"
            placeholder=" "
            value={body}
            onChange={(e) => setBody(e.target.value)}
          />
        </div>
        <button className="btn btn-primary" onClick={handleUpdate}>
           
        </button>
      </section>
    </div>
  );
}
