import { useParams, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { doc, getDoc, deleteDoc } from "firebase/firestore";
import { db } from "../firebase";
import dayjs from "dayjs";
import { alertConfirm, alertInfo } from "../utils/commonUtils";
import { useAuth } from "../AuthContext";

export default function BoardDetail() {
  const { user } = useAuth();
  const { id } = useParams();
  const navigate = useNavigate();
  const [post, setPost] = useState(null);

  const isOwner = user && post && post.ownerUid === user.uid;
  useEffect(() => {
    const loadPost = async () => {
      const docRef = doc(db, "board", id);
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) setPost({ id: docSnap.id, ...docSnap.data() });
      else setPost(null);
    };
    loadPost();
  }, [id]);

  const handleDelete = () => {
    if (!user) return alertInfo(" .", () => navigate("/login"));
    if (!isOwner) return alertInfo("    .");
    alertConfirm("?", async () => {
      await deleteDoc(doc(db, "board", id));
      alertInfo(".");
      navigate("/board");
    });
  };

  const handleEdit = () => {
    if (!user) return alertInfo(" .", () => navigate("/login"));
    if (!isOwner) return alertInfo("    .");
    navigate(`/board/edit/${id}`);
  };
  if (post == null) return <div style={{ padding: "2rem" }}>  .</div>;

  return (
    <div className="page">
      <div className="header"> </div>
      <div className="intro-section" style={{ padding: "1.25rem" }}>
        <h2 style={{ marginBottom: ".25rem" }}>{post.title}</h2>
        <div className="meta" style={{ marginBottom: ".75rem" }}>
          : <b>{post.authName || ""}</b> {" "}
          {post.date?.seconds ? dayjs(post.date.seconds * 1000).format("YYYY-MM-DD HH:mm") : " "}
        </div>
        <hr style={{ margin: "1rem 0" }} />
        <p style={{ whiteSpace: "pre-wrap", lineHeight: 1.6 }}>{post.body || " "}</p>

        {isOwner && (
          <div style={{ marginTop: "1rem", display: "flex", gap: ".5rem" }}>
            <button className="btn btn-ghost" onClick={handleEdit}>
               
            </button>
            <button className="btn btn-danger" onClick={handleDelete}>
               
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
