import { useEffect, useState } from "react";
import { db } from "../firebase";
import { collection, getDocs } from "firebase/firestore";
import dayjs from "dayjs";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../AuthContext";
import { alertInfo } from "../utils/commonUtils";

export default function BoardList() {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [posts, setPosts] = useState([]);
  const [search, setSearch] = useState("");

  useEffect(() => {
    loadPosts();
  }, []);

  const handleRowClick = (id) => {
    navigate(`/board/${id}`);
  };

  const loadPosts = async () => {
    const querySnapshot = await getDocs(collection(db, "board"));
    let result = [];
    querySnapshot.forEach((doc) => {
      result.push({ id: doc.id, ...doc.data() });
    });
    result.sort((a, b) => (b.date?.seconds || 0) - (a.date?.seconds || 0));
    setPosts(result);
  };

  const filteredPosts = posts.filter((post) => (post.title || "").toLowerCase().includes(search.toLowerCase()));

  const write = () => {
    if (!user) {
      alertInfo(" .", () => navigate("/login"));
      return;
    }
    navigate("/board/write");
  };

  return (
    <div className="page">
      <div className="header"></div>
      <section className="intro-section">
        <div className="toolbar">
          <button className="btn btn-primary" onClick={write}>
            
          </button>
          <div className="toolbar-right">
            <input
              className="input"
              type="text"
              placeholder=" "
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
          </div>
        </div>
        <table className="worship-table">
          <thead>
            <tr>
              <th className="worship-cell"></th>
              <th className="worship-cell"></th>
              <th className="worship-cell"></th>
            </tr>
          </thead>
          <tbody>
            {filteredPosts.length === 0 && (
              <tr>
                <td colSpan="3" className="empty">
                   .
                </td>
              </tr>
            )}
            {filteredPosts.map((post) => (
              <tr key={post.id} onClick={() => handleRowClick(post.id)} style={{ cursor: "pointer" }}>
                <td className="worship-cell">{post.title}</td>
                <td className="worship-cell">{post.authName || ""}</td>
                <td className="worship-cell">
                  {post.date?.seconds ? dayjs(post.date.seconds * 1000).format("YYYY-MM-DD HH:mm") : " "}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>
    </div>
  );
}
