import { useEffect, useState } from "react";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";
import { db } from "../firebase";
import { useNavigate } from "react-router-dom";
import { alertConfirm, alertInfo } from "../utils/commonUtils";
import { useAuth } from "../AuthContext";

export default function BoardWrite() {
  const { user } = useAuth();
  const navigate = useNavigate();

  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");

  const handleSubmit = async () => {
    if (!title.trim()) return alertInfo(" ");
    if (!user) return alertInfo(" .", () => navigate("/login"));

    alertConfirm("?", async () => {
      await addDoc(collection(db, "board"), {
        title,
        body,
        authName: user.displayName || "",
        ownerUid: user.uid,
        creDate: serverTimestamp(),
        file: ""
      });
      alertInfo("!");
      navigate("/board");
    });
  };

  useEffect(() => {
    if (!user) {
      alertInfo(" .", () => navigate("/login"));
    }
  }, [user, navigate]);

  return (
    <div className="page">
      <div className="header"></div>
      <section className="intro-section">
        <div className="meta" style={{ marginBottom: ".5rem" }}>
          : <b>{user?.displayName || ""}</b>
        </div>

        <div className="form-row">
          <input
            className="input"
            type="text"
            placeholder=""
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>
        <div className="form-row">
          <textarea
            className="textarea"
            placeholder=" "
            value={body}
            onChange={(e) => setBody(e.target.value)}
          />
        </div>
        <div className="form-row">
          <button className="btn btn-primary" onClick={handleSubmit}>
             
          </button>
        </div>
      </section>
    </div>
  );
}
